import React, { ReactNode, Suspense } from 'react';
import { Carousel, IconButton } from "@material-tailwind/react";
import { LoadingSpinner } from './LoadingSpinner';

type ContentCarouselProps = {
  children: ReactNode
}

const CarouselArrow = ({ clickHandler }) => (
  <IconButton
    onClick={clickHandler}
    variant="text"
    color="white"
    size="lg"
    className="!absolute top-2/4 left-4 -translate-y-2/4 visible sm:invisible"
  >
    <svg
      xmlns="http://www.w3.org/2000/svg"
      fill="none"
      viewBox="0 0 24 24"
      strokeWidth={2}
      stroke="currentColor"
      className="h-6 w-6"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        d="M10.5 19.5L3 12m0 0l7.5-7.5M3 12h18"
      />
    </svg>
  </IconButton>
)

const ContentCarousel = ({ children }: ContentCarouselProps) => (
  <Carousel
    children={children}
    className='relative rounded-lg h-full w-full'
    nextArrow={({ handleNext }) => (<CarouselArrow clickHandler={handleNext} />)}
    prevArrow={({ handlePrev }) => (<CarouselArrow clickHandler={handlePrev} />)}
    navigation={({ setActiveIndex, activeIndex, length }) => (
      <div className="absolute bottom-4 left-2/4 z-50 flex -translate-x-2/4 gap-2">
        {new Array(length).fill("").map((_, i) => (
          <span
            key={i}
            className={`block h-1 cursor-pointer sm:cursor-default rounded-2xl transition-all content-[''] ${activeIndex === i ? "w-8 bg-white" : "w-4 bg-white/50"} visible sm:hidden`}
            onClick={() => setActiveIndex(i)}
          />
        ))}
      </div>
    )}
  />
)

export const LazyContentCarousel = (props) => (
  <Suspense fallback={<LoadingSpinner />}>
    <ContentCarousel {...props} />
  </Suspense>
);

export default LazyContentCarousel;